html {
  font-family: monospace;
}

body {
  margin: 0;
  padding: 0;
  background-color: #2b2b2b;
}

.App {
  color: #CCC;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App > * {
  margin-bottom: .5rem;
}

.oneline {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.oneline > * {
    margin: .5em;
}
.Input__field:focus, .Input__field:hover {
    outline: none;
    background-color: rgba(255, 255, 255, .2);
    border-bottom-width: .4rem;
}

.Input__field {
    font-size: 3rem;
    width: 4.5em;
    text-align: center;
    text-transform: uppercase;
    background-color: transparent;
    border: none;
    border-bottom: solid .2rem rgba(255, 255, 255, .2);
    color: #CCC;
    -webkit-transition: .3s;
    transition: .3s;
}

.Input__label {
    color: #CCC;
    margin-right: 0.5rem;
    font-size: 4rem;
    margin-bottom: 0.2rem;
}

.Input__flex {
    display: flex;
    align-items: center;
    justify-content: center;
}


.BarChart {
    background-color: darkslategrey;
    border-radius: 2em 2em 0 0;
    height: calc(16vw + 7em);
    width: calc(12vw + 5em);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(100, 1fr);
    grid-column-gap: 5px;
}

[id^="Bar-"] {
    grid-row-end: 101;
    border-radius: 2em 2em 0 0;
}

#Bar-r {
    grid-column-start: 1;
    grid-column-end: 2;

    grid-row-start: 1;
    background-color: #f00;
}

#Bar-g {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    background-color: #0f0;
}

#Bar-b {
    grid-row-start: 1;
    grid-column-start: 3;
    grid-column-end: 4;
    background-color: #00f;
}
.hslwheel {
    padding: 0;
    font-size: calc(.5vw + .35em);
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30em;
    width: 30em;
}

.hslwheel .needle {
    height: 4em;
    width: 16em;
    border-radius: 2em;
    background-color: rgba(255, 255, 255, .3);
    -webkit-transition: .3s ;
    transition: .3s ;
}

.hslwheel > li:hover {
    border: .3em solid rgba(255, 255, 255, 0.7);
    cursor: pointer;
}

.hslwheel > li:active {
    opacity: .5;
    cursor: pointer;
}

.hslwheel > li {
    position: absolute;
    height: 5.5em;
    width: 5.5em;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    text-align: center;
    text-shadow: 0 0 1px rgba(0, 0, 0, .5);
}

:root {
    --offset: 12em;
}

.hslwheel .red {
    background-color: #F00;
    -webkit-transform: rotate(-90deg) translateX(12em);
            transform: rotate(-90deg) translateX(12em);
    -webkit-transform: rotate(-90deg) translateX(var(--offset));
            transform: rotate(-90deg) translateX(var(--offset));
}

.hslwheel .red span {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

